<template>
    <div class="box">
        <Header  :nnt="true" :got="true" :ont="true" :gnt="false">

        </Header>
        <div class="content">
            <div class="item">
                <ul v-for="(item,index) of info" :key="index">
                    <li>
                        <p>{{item.address}}</p>
                        <p>{{item.name}}</p>
                    </li>
                    <li @click="address(item.id)">X</li>
                </ul>
            </div>
            <div class="address">
                <router-link to="/anout/receiv/add">
                    新增地址
                </router-link>
                
            </div>
        </div>
        <!-- <van-icon name="arrow-left" /> -->
    </div>
</template>

<script>
import Header from '../header/Header'
import axios from 'axios'
import { Icon } from 'vant';
    export default {
        components: {
            Header
        },
        data () {
            return {
                info:[]      
            }
        },
        mounted() {
            this.http.get("http://elm.cangdu.org/v1/users/38070/addresses").then((res)=>{
                this.info=res.data
            })
        },
        methods: {
            address(id){
                axios.delete("http://elm.cangdu.org/v1/users/38070/addresses/"+id).then((res)=>{
                    axios.get("http://elm.cangdu.org/v1/users/38070/addresses").then((res)=>{
                        this.info=res.data
                    })
                })
            }
        }
    }
</script>

<style lang="scss" scoped>
     .box{
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        font-size: 0.35rem;
    }
    .content{
        width: 100%;
        flex: 1;
        overflow: auto;
        font-size: 0.35rem;
        background: rgb(240, 234, 234);
        .item{
            width: 100%;
            // height: 1.5rem;
            border-bottom: 0.3rem solid whitesmoke;
            background: white;
            ul{
                width: 100%;
                height: 1.5rem;
                border-bottom: 0.01rem solid rgb(197, 185, 185);
                display: flex;
                li{
                    line-height: 1.5rem;
                    p{
                        line-height: 0.7rem;
                        // padding-top: 0.3rem;
                    }
                }
                li:nth-of-type(1){
                    flex: 10;
                    margin: 0 0.2rem;
                }
                li:nth-of-type(2){
                    flex: 1;
                    // margin: 0 0.2rem;
                }
            }
        }
        .address{
            width: 100%;
            height: 1rem;
            line-height: 1rem;
            padding: 0 0.2rem;
            background: white;
        }
    }
</style>


